:root{
	font-size: 10px;
}
@font-face{
	font-family: MontserratBold;
	src: url(../fonts/MontserratBold.ttf);
}
@font-face{
	font-family: MontserratRegular;
	src: url(../fonts/MontserratRegular.ttf);
}
@font-face{
	font-family: MontserratSemibold;
	src: url(../fonts/MontserratSemiBold.ttf);
}

$white-color: #fff;
$contact-color: #191e60;
$blue-color: #3a4ba5;
$lightblue-color: #071739;
$sidebar-bg: #f2f2f2;
$slider-mask: rgba(58, 75, 165, .9);
$button-padding: 15px 34px;
$button-fz: 1.67rem;
$subtext: 1.45rem;
$contact-fz: 2.5rem;
$semibold: MontserratSemibold, sans-serif;
$bold: MontserratBold, sans-serif;

body{
	margin: 0;
	overflow-x: hidden;
	font-family: MontserratRegular, sans-serif;
}
*,
*:before,
*:after{
	box-sizing: border-box;
}
a{
	text-decoration: none;
}
ul{
	padding: 0;
	margin: 0;
	list-style-type: none;
}
.button{
	outline: none;
	border: none;
	cursor: pointer;
	background: $white-color;
	font-size: $button-fz;
	padding: $button-padding;
	font-family: $bold;
	border-radius: 25px;
	transition: .2s linear;
}
.button:hover{
	opacity: .6;
}
.button--blue{
	background: $blue-color;
	color: $white-color;
}
.button--white{
	border: 2px solid $blue-color;
	color: $blue-color;
}

/* Header */
/* Header top */

.header__top--inner{
	display: flex;
	align-items: center;
}
.header__menu--inner{
	display: flex;
	flex-direction: column;
	width: 100%;
	border-left: 1px solid #eaeaea;
}
.header__menu--top{
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 30px 50px;
	border-bottom: 1px solid #eaeaea;
}
.header__menu--bottom{
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0px 50px;
	width: 100%;
}
.header__logo{
	padding: 0px 25px;
}
.header__title{
	font-size: 2.1rem;
	color: $lightblue-color;
	font-family: $semibold;
}
.header__span--blue{
	color: $blue-color;
	font-family: $bold;
}

/* Header bottom */

.header__menu--link{
	font-size: $button-fz;
	color: $lightblue-color;
	margin: 16px 0px;
	font-family: $semibold;
	transition: .2s linear;
}
.header__menu--link:hover{
	color: $blue-color;
}
.menu__btn{
	display: none;
}
.header__menu--link:first-child{
	padding-left: 0;
}
.header__menu--link:nth-child(6){
	margin-right: 10px;
}
.header__menu--glasses{
	margin: 0;
	padding: 16px 40px 16px 100px;
	background: $blue-color;
	color: $white-color;
	position: relative;
	transition: .2s linear;
}
.glasses{
	font-size: $contact-fz;
	position: absolute;
	top: 50%;
	left: 40px;
	transform: translateY(-50%);
}
.header__menu--glasses:hover{
	color: #000;
	opacity: .9;
}
.header__menu--container, .header__menu--bottom--inner{
	display: flex;
	justify-content: space-between;
}
.header__menu--bottom--inner{
	width: 50%;
}
.header__menu--bottom:nth-child(1){
	padding-right: 0;
}

/* Header slider left */

.header__slider--inner{
	display: flex;
	align-items: center;
}
.header__slider--left{
	min-height: 421px;
	width: 44%;
	padding-top: 50px;
	padding-bottom: 50px;
	padding-right: 50px;
	padding-left: calc(100vw - 1700px);
	background: $lightblue-color;
}
.header__slider--right{
	width: 56%;
	height: 421px;
	overflow: hidden;
}
.slider__item--title{
	font-size: $contact-fz;
	color: $white-color;
	font-family: $bold;
}
.slider__item--text{
	font-size: $button-fz;
	color: $white-color;
	font-family: $semibold;
}
.slick__button{
	border: none;
	padding: 15px 74px;
	margin-top: 60px;
}
li.slick-active{
    width: 1.3rem;
    height: 1.3rem;
    background: $blue-color;
    border-radius: 50%;
}
li.slick-active:hover{
	background: $blue-color;
}
li{
    display: inline-block;
    width: 1.3rem;
    height: 1.3rem;
    border-radius: 50%;
    margin-right: 17px;
    cursor: pointer;
    background: $white-color;
    transition: .2s linear;
}
li:hover{
    background: #838b9c;
}
.comment__choise li button{
	opacity: 0;
	cursor: pointer;
}
.comment__choise{
	position: relative;
	top: 93px;
	left: 350px;
}

/* Header slider right */

.slider__arrow, .footer__slider--arr{
	width: 50px;
	height: 50px;
	border-radius: 50%;
	background: $blue-color;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	position: absolute;
	z-index: 1000;
	bottom: 50px;
	transition: .2s linear;
}
.slider__arrow--right, .footer__arrow--right{
	left: 120px;
}
.slider__arrow--left, .footer__arrow--left{
	left: 50px;
}
.slider__arrow:hover{
	background: $white-color;
}
.slider__arrow:hover .arrow{
	color: $blue-color;
}
.arrow{
	color: $white-color;
	font-size: 1.5rem;
}
.slider__right--img{
	position: relative;
	top: -307px;
}
.slider__right{
	width: 100%;
	height: 100%;
}

/* Strukture */

.strukture{
	display: flex;
	justify-content: space-between;
}
.left__sidebar{
	position: relative;
	min-width: 360px;
	padding: 50px 0;
	box-shadow: 0px -24px 30px 0px rgba(3, 3, 57, 0.2);
}
.left__sidebar--column{
	border-bottom: 1px solid $sidebar-bg;
}
.left__sidebar--column:last-child{
	border-bottom: 0;
}
.column__item, .column__item-2, .column__item--vlo, .column__item--vlo--vlo{
	cursor: pointer;
	padding: 25px 35px 25px 50px;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	align-items: center;
	transition: .2s linear;
}
.column__item--vlo{
	padding-left: 75px;
	display: none;
}
.column__item--vlo--vlo{
	padding: 10px 10px 10px 75px;
	display: none;
}
.column__item--vlos{
	border-left: 1px solid $sidebar-bg;
	padding-left: 25px;
}
.column__item--text, .fa-plus, .fa-minus, .column__item-2, .column__item--text--vlo{
	color: $lightblue-color;
	font-size: $button-fz;
	font-family: $semibold;
}
.column__item--text--vlo{
	font-size: $subtext;
	display: inline-block;
	margin-top: 10px;
}
.column__item:hover, .column__item-2:hover, .column__item--vlo:hover{
	background: $sidebar-bg;
}
.column__item--text{
	margin-right: 15px;
}
.blue{
	color: $blue-color;
}
.active{
	background: $blue-color;
}
.active .column__item--text{
	color: $white-color;
}
.active:hover{
	background: $blue-color;
}
.active:hover .column__item--text{
	color: $white-color;
}
.fa-minus{
	color: $white-color;
}
.active .fa-plus{
	display: none;
}
.column__item.active .fa-minus, .column__item--vlo.active .fa-minus{
	display: inline-block;
}
.column__item .fa-minus, .column__item--vlo .fa-minus{
	display: none;
}
.column__item--vlo.active__vlo .fa-minus{
	display: inline-block;
}
.column__item--vlo.active__vlo .fa-plus{
	display: none;
}
.column__item--vlo .fa-minus{
	color: #000;
}

/* News */

.news{
	position: relative;
	padding: 35px;
}
.news__title{
	font-size: 3.33rem;
	font-family: $semibold;
	color: $lightblue-color;
	text-align: center;
}
.news__blocks{
	max-width: 1100px;
	display: flex;
	flex-direction: column;
}
.news__inner{
	display: flex;
	align-items: center;
	margin-top: 35px;
}
.news__small--block{
	position: relative;
}
.news__small--block:hover .news__mask{
	position: absolute;
	cursor: default;
	bottom: 2px;
	left: 0;
	width: 340px;
	height: 241px;
	z-index: 55;
	background: $slider-mask;
}
.news__big--block:hover .news__mask{
	position: absolute;
	cursor: default;
	bottom: 2px;
	left: 0;
	width: 720px;
	height: 520px;
	z-index: 55;
	background: $slider-mask;
}
.slider__mask--button{
	display: none;
}
.news__small--block:hover .slider__mask--button{
	color: $blue-color;
	display: block;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 555;
}
.news__big--block:hover .slider__mask--button{
	color: $blue-color;
	display: block;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 555;
}
.news__big--block{
	position: relative;
}
.big__img{
	max-width: 720px;
}
.mr--img{
	margin-right: 35px;
}
.mt--img{
	margin-top: 35px;
}
.text, .date, .date--big{
	max-width: 285px;
	color: $white-color;
	font-family: $semibold;
	font-size: $subtext;
}
.date, .date--big{
	margin-top: 20px;
}
.news__ab--text{
	position: absolute;
	bottom: 25px;
	left: 25px;
}
.text--big{
	max-width: 645px;
	color: $white-color;
	font-family: $semibold;
	font-size: 2.1rem;
}
.date--big{
	font-size: 1.6rem;
}
.news__button--bottom{
	margin: 35px 0;
}
.button__bottom{
	display: flex;
	justify-content: center;
}
.news__title--line{
	border-top: 1px solid $sidebar-bg;
	padding-top: 35px;
}
.news__actual--inner{
	display: flex;
	align-items: center;
	flex-direction: column;
}
.news__actual--box{
	display: flex;
	margin-top: 35px;
}
.button__bottom--inner{
	display: flex;
	justify-content: center;
	padding: 50px 0;
}

/* Right sidebar */

.right__sidebar{
	position: relative;
	padding: 150px 50px;
	max-width: 380px;
	background: $sidebar-bg;
} 
.right__sidebar--item{
	margin-top: 50px;
}
.right__arrows{
	font-size: 2.1rem;
	color: $white-color;
}
.right__arrow{
	cursor: pointer;
	z-index: 555;
	position: absolute;
	width: 50px;
	height: 50px;
	border-radius: 50%;
	background: $blue-color;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: .3s linear;
}
.right__arrow:hover{
	opacity: .6;
}
.right__slider--arrow--up{
	top: -50px;
	right: 50%;
	transform: translateX(40%);
}
.right__slider--arrow--down{
	bottom: -100px;
	right: 50%;
	transform: translateX(40%);
}
.fas.krestik{
	color: #000;
	font-size: 2rem;
	position: absolute;
	top: 10px;
	right: 10px;
}






/* Modal */

.modal__menu--header{
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	z-index: 99999;
	opacity: .9;
	background: $blue-color;
	padding: 60px 50px;
}
.modal__menu--inner{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
.modal__link{
	display: inline-block;
	margin-top: 20px;
	font-size: 1.6rem;
	color: $white-color;
	font-family: $bold;
	transition: .2s linear;
}
.modal__link:hover{
	color: $lightblue-color;
}
.button--modal{
	color: $blue-color;
}
.fa-times{
	font-size: 2rem;
	color: $white-color;
	cursor: pointer;
}
.fas.ar__right{
	cursor: pointer;
	font-size: 2.5rem;
	position: absolute;
	top: 50px;
	left: 50px;
	display: none;
}
.fas.krestik{
	display: none;
}



/* Footer Slider */

.footer__slider{
	padding: 50px 205px;
	background: #f2f2f2;
}
.footer__slider--title{
	font-size: 3.33rem;
	color: $lightblue-color;
	font-family: $semibold;
	text-align: center;
}
.footer__slider--inner{
	position: relative;
	margin-top: 50px;
}
.slider__link{
	margin-right: 50px;
	position: relative;
	cursor: default;
	transition: .2s linear;
}
.slider__link:hover .slider__mask{
	position: absolute;
	cursor: default;
	top: 0;
	left: 0;
	width: 260px;
	height: 260px;
	background: $slider-mask;
}
.slider__mask--button{
	display: none;
}
.slider__link:hover .slider__mask--button{
	color: $blue-color;
	display: block;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 555;
}
.slider__mask--button:hover{
	opacity: 1;
}
.footer__arrow--right{
	left: 1480px;
    top: 50%;
    transform: translateY(-50%);
}
.footer__slider--arr:hover{
	opacity: .6;
}
.footer__arrow--left{
	left: -80px;
    top: 50%;
    transform: translateY(-50%);
}
.footer__contact--inner{
	display: flex;
	padding: 50px 0;
	border-bottom: 1px solid #e6e7eb;
}
.footer__contact--inner:last-child{
	border-bottom: 0;
}
.footer__contact{
	padding: 50px 180px;
}
.contact__title{
	font-size: $contact-fz;
	color: $contact-color;
	font-family: $bold;
}
.contact__text{
	color: $lightblue-color;
}
.contact__link{
	transition: .2s linear;
	color: $blue-color;
}
.contact__link:hover{
	opacity: .6;
}
.contact__link, .contact__text{
	margin-top: 25px;
	font-size: $button-fz;
	font-family: $semibold;
}
.address, .email, .phone{
	width: 25%;
	margin-right: 50px;
}
.address{
	width: 35%;
}
.hours{
	width: 40%;
}
.graphic{
	width: 28%;
}
.footer__menu{
	margin-top: 50px;
	padding: 50px 170px 35px 170px;
	background: $lightblue-color;
}
.footer__menu--item{
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	width: 20%;
}
.footer__menu--item:last-child{
	width: 40%;
	justify-content: space-between;
	align-items: flex-end;
}
.footer__menu--inner{
	display: flex;
}
.footer__menu--title{
	font-size: $contact-fz;
	color: $white-color;
	font-family: $bold;
}
.footer__menu--links{
	margin-top: 50px;
}
.footer__menu--link{
	color: $white-color;
	font-size: $subtext;
	font-family: $semibold;
	margin-top: 10px;
	display: inline-block;
}
.footer__button{
	border: none;
}
.fa-long-arrow-alt-up{
	margin-left: 7px;
}
.footer__grey{
	font-size: $subtext;
	color: #757575;
	font-family: $semibold;
}
.copyright{
	font-family: $semibold;
	color: $white-color;
	font-size: $subtext;;
}
.footer__light{
	font-family: MontsseratRegular;
}
.footer__menu--adress, .menu__link{
	color: $white-color;
	font-size: $subtext;
	margin-top: 16px;
	display: inline-block;
}
.footer__menu--adress{
	margin-top: 60px;
}

/* Media */ 

@media(max-width: 1880px){
	.right__sidebar{
		display: none;
	}
	.button__bottom--inner{
		margin-left: 360px;
	}
	.news{
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.footer__menu--item{
		width: 25%;
	}
}
@media(max-width: 1800px){
	.footer__arrow--right {
	    left: 1325px;
	}
}
@media(max-width: 1725px){
	.header__menu--top{
		padding: 25px 20px;
	}
	.header__menu--link{
		font-size: 1.5rem;
	}
	.button{
		font-size: 1.5rem;
		padding: 13px 20px;
	}
	.header__slider--left{
		padding: 50px;
	}
	.footer__menu--item{
		width: 50%;
		margin-top: 60px;
		padding-right: 20px;
	}
	.footer__menu--item:last-child{
		width: 50%;
	}
	.footer__menu--inner{
		flex-wrap: wrap;
	}
	.footer__arrow--right {
	    left: 1325px;
	}
}
@media(max-width: 1700px){
	.footer__arrow--right {
	    left: 1245px;
	}
}
@media(max-width: 1545px){
	.header__menu--top{
		padding: 20px 20px;
	}
	.header__menu--link{
		font-size: 1.4rem;
	}
	.button{
		font-size: 1.4rem;
		padding: 11px 17px;
	}
	.header__logo{
		width: 175px;
	}
	.header__title{
		font-size: 1.7rem;
	}
	.header__menu--bottom{
		padding: 0px 20px;
	}
	.left__sidebar{
		position: absolute;
		z-index: 9999999;
		background: $white-color;
	}
	.button__bottom--inner{
		margin-left: 0;
	}
	.fas.ar__right{
		display: inline-block;
	}
	.fas.krestik{
		display: inline-block;
	}
	.footer__arrow--right {
	    left: 590px;
	}
	.footer__slider--inner{
		max-width: 600px;
		margin: 50px auto;
	}
}
@media(max-width: 1385px){
	.comment__choise {
	    position: relative;
	    top: 86px;
	    left: 125px;
	}
	.slider__item--title{
		font-size: 2rem;
	}
	.slider__item--text{
		font-size: 1.3rem;
	}
}
@media(max-width: 1370px){
	.header__menu--container{
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.header__menu--bottom{
		justify-content: space-between;
		width: 100%;
	}
	.header__menu--bottom--inner{
		width: 70%;
	}
	.header__logo{
		width: 150px;
	}
	.header__title{
		font-size: 1.4rem;
	}
	.button{
		font-size: 1.2rem;
		padding: 10px 14px;
	}
	.header__slider--left{
		padding: 30px;
	}
	.contact__title{
		font-size: 2rem;
	}
	.contact__text, .contact__link{
		font-size: 1.3rem;
	}
}
@media(max-width: 1140px){
	.header__logo{
		width: 170px;
	}
	.header__title{
		font-size: 1.4rem;
		margin-right: 10px;
	}
	.button{
		margin-right: 10px;
		text-align: center;
	}
	.header__slider--inner{
		flex-direction: column;
	}
	.header__slider--left, .header__slider--right{
		width: 100%;
	}
	.header__slider--left{
		min-height: 250px;
	}
	.news__inner{
		flex-direction: column;
		margin-top: 0;
	}
	.mr--img{
		margin-right: 0;
	}
	.mt--img{
		margin-top: 0;
	}
	.news__big--block{
		margin-top: 40px;
	}
	.news__small--block{
		margin-top: 40px;
	}
	.news__actual--box{
		flex-direction: column;
		margin-top: 0;
	}
	.footer__contact--inner{
		justify-content: center;
		flex-wrap: wrap;
	}
	.phone, .hours, .email, .address, .boss, .graphic{
		width: 100%;
		margin: 30px 0 0 0;
	}
}
@media(max-width: 1030px){
	.header__menu--bottom--inner{
		width: 95%;
	}
	.footer__menu--title{
		font-size: 2rem;
	}
	.footer__menu--link{
		font-size: 1.3rem;
	}
	.footer__slider{
		padding: 50px;
	}
}
@media(max-width: 810px){
	.header__menu--bottom--inner{
		display: none;
	}
	.menu__btn{
		display: block;
		padding: 20px 0;
	}
	.btn--none{
		display: none;
	}
	.footer__menu--item{
		width: 100%;
		margin-top: 60px;
		padding-right: 20px;
	}
	.footer__menu--item:last-child{
		width: 100%;
	}
	.footer__menu--inner{
		flex-wrap: wrap;
	}
	.copyright{
		margin-top: 30px;
	}
	.footer__slider--inner{
		max-width: 300px;
	}
	.footer__arrow--right {
	    left: 275px;
	}
	.footer__arrow--left{
		left: -63px;
	}
}
@media(max-width: 778px){
	.news__big--block{
		display: none;
	}
	.footer__contact{
		padding: 50px;
	}
	.footer__menu{
		padding: 50px 30px;
	}
}
@media(max-width: 450px){
	.header__title{
		font-size: 1.2rem;
	}
	.fas.ar__right{
		top: 42px;
